body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 15px;
  border: 2px solid black;
  width: 500px;
  justify-content: center;
  margin: 0 auto;
}

form input, textarea{
  width: 400px;
  text-align: center;
  height: 30px;
  margin: 10px;
  border-radius: 10px;
}

form textarea {
  height: 60px;
}

form button {
  height: 30px;
  border-radius: 10px;
  margin: 5px;
}

.check_container {
  width: 80%;
  margin: 0 auto;
}
.check-container input {
  width: auto;
  margin: 5px;
  padding: 3px;
  height: auto;
}

#input_container_closed {
  display: none;
}
#input_container_open {
  display: block;
}

#open_close {
  width: 30px;
}

/* ================Items container */

.items-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  margin: 0 auto;
  width: 80%;
}

.item-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 1fr;
  height: 400px;
  border: solid 1px black;
  margin: 10px;
}

.item-container p {
  width: 80%;
}